import React,{ useState, useEffect }  from 'react';
import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import chat from './chat';
import chatlist from './chatlist';
import topbanner from './topbanner';

import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import { sizing } from '@material-ui/system';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

const routes = [
  {
    path: "/",
    exact: true,
    sidebar: () => <div>home!</div>,
    main: () => <h2>Home</h2>
  },
  {
    path: "/bubblegum",
    sidebar: () => <div>bubblegum!</div>,
    main: () => chatlist()
  },
  {
    path: "/shoelaces",
    sidebar: () => <div>shoelaces!</div>,
    main: ()=>chat()
  }
];

const useStyles = makeStyles({
  root: {
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    width: '100%',

    bottom: 0,
  },
});


function SidebarExample() {
  const [ style, setStyle ] = useState({})
  fetch("mydata.json")
              .then(res => res.json())
              .then(setStyle)

  const classes = useStyles();
  const [value, setValue] = React.useState(0);

    return (

      <Router>
          <div style={{height:"100vh",background:"grey"}}>
              <div style={{margin:"auto","max-width":"780px",height:"100vh",background:"blue","min-height":"300px","max-height":"1334px"}}>
          <div style={{"min-height":"100%"}}>

       <div style={{"padding-bottom":"75px"}}>

       <div style={{height:"30px",background:"red"}}>

         {topbanner()}
       </div>

          <div style={{height:"100%","overflow-y":"auto","max-height":"1334px"}}>
            <Switch>
              {routes.map((route, index) => (
                  // Render more <Route>s with the same paths as
                  // above, but different components this time.
                  <Route
                      key={index}
                      path={route.path}
                      exact={route.exact}
                      children={<route.main />}
                  />
              ))}
            </Switch>

          </div>
       </div>
          </div>

           <div style={{position:"relative","margin-top":"-75px",clear:"both",height:"75px",background:"green"}}>
        <BottomNavigation
            value={value}
            onChange={(event, newValue) => {
              setValue(newValue);
            }}
            showLabels
            //className={classes.root}
        >
          <BottomNavigationAction label="Recents" icon={<RestoreIcon />} component={Link} to="/"/>
          <BottomNavigationAction label="Favorites" icon={<FavoriteIcon />} component={Link} to="/bubblegum"/>
          <BottomNavigationAction label="Nearby" icon={<LocationOnIcon />} component={Link} to="/shoelaces"/>
        </BottomNavigation>
           </div>
              </div>
          </div>
      </Router>
  );
}




function App() {

  return (

          <SidebarExample/>

  );
}

export default App;
